import React, { Component, Fragment } from 'react';

export default class ListItem extends Component {
    constructor(props) {
        super(props);
        this.state = {
            class: 'nav-item',
        }
        this.handleMouseEnter = this.handleMouseEnter.bind(this);
        this.handleMouseLeave = this.handleMouseLeave.bind(this);
        this.handleLogin = this.handleLogin.bind(this);
    }

    handleLogin() {
        document.getElementById('login').style.visibility = 'visible';
        document.getElementsByClassName('login-modal')[0].classList.add('jo');
    }
    
    handleMouseEnter() {
        this.setState({
            class: this.state.class + ' is-focused'
        })
    }

    handleMouseLeave() {
        this.setState({
            class: 'nav-item'
        })
    }

    render() {
        const result = (child) => {
            if (child !== undefined) {
                return (
                    <div className="nav-dropdown">
                        <div className="nav-dropdown-container">
                            {
                                child.map(item => (
                        
                                    <div className="nav-dropdown-column">
                                        {
                                            item.map(chunk => (
                                                <a className={chunk.class} href={chunk.href}>{chunk.text}</a>
                                            ))
                                        }
                                    </div>
                                ))
                            }
                        </div>
                    </div>
                );
            }
            return <Fragment></Fragment>
        }
        return (
            <li onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave} className={this.state.class}>
                {
                    (this.props.href) ? <a href={this.props.href}>{this.props.text}</a> : <button type={this.props.type} onClick={this.handleLogin}>{this.props.text}</button>
                }
                { result(this.props.child) }
            </li>
        );
    }
}