import React, { Component, Fragment } from 'react';
import ReactDOM from 'react-dom';
import { SingleProduct } from '../Products/Products';
import '../../styles/flashsale.css';
import '../../styles/products.css';
import Slider from '../Slider/Slider';

const flashsales = [
    {
        image: 'assets/images/1.png',
        href: '#',
        stars: Math.random() * 2 + 3,
        numberOfRatings: Math.random() * 300 + 10,
        name: 'SPECIAL 001',
        salePrice: 1190000,
        originPrice: 2190000
    }, {
        image: 'assets/images/2.png',
        href: '#',
        stars: Math.random() * 2 + 3,
        numberOfRatings: Math.random() * 300 + 10,
        name: 'SPECIAL 002',
        salePrice: 1190000,
        originPrice: 2190000
    }, {
        image: 'assets/images/3.png',
        href: '#',
        stars: Math.random() * 2 + 3,
        numberOfRatings: Math.random() * 300 + 10,
        name: 'SPECIAL 003',
        salePrice: 1190000,
        originPrice: 2190000
    }, {
        image: 'assets/images/4.png',
        href: '#',
        stars: Math.random() * 2 + 3,
        numberOfRatings: Math.random() * 300 + 10,
        name: 'SPECIAL 004',
        salePrice: 1190000,
        originPrice: 2190000
    },
    {
        image: 'assets/images/1.png',
        href: '#',
        stars: Math.random() * 2 + 3,
        numberOfRatings: Math.random() * 300 + 10,
        name: 'SPECIAL 001',
        salePrice: 1190000,
        originPrice: 2190000
    }, {
        image: 'assets/images/2.png',
        href: '#',
        stars: Math.random() * 2 + 3,
        numberOfRatings: Math.random() * 300 + 10,
        name: 'SPECIAL 002',
        salePrice: 1190000,
        originPrice: 2190000
    }, {
        image: 'assets/images/3.png',
        href: '#',
        stars: Math.random() * 2 + 3,
        numberOfRatings: Math.random() * 300 + 10,
        name: 'SPECIAL 003',
        salePrice: 1190000,
        originPrice: 2190000
    }, {
        image: 'assets/images/4.png',
        href: '#',
        stars: Math.random() * 2 + 3,
        numberOfRatings: Math.random() * 300 + 10,
        name: 'SPECIAL 004',
        salePrice: 1190000,
        originPrice: 2190000
    }
];


class FlashSale extends Component {
    constructor(props) {
        super(props);
        this.state = {
            start: 0,
        }
        this.getTime = this.getTime.bind(this);
        this.getClock = this.getClock.bind(this);
    }

    getTime() {
        let hours = new Date().getHours();
        hours = (hours < 10) ? '0' + hours : hours;
        return hours + ':00:00';
    }

    getClock() {
        const today = new Date
        const minutes = 60 - today.getMinutes(), seconds = 60 - today.getSeconds();
        ReactDOM.render(
        <Fragment> 
            <span className="clockcount">00</span>:
            <span className="clockcount">{(minutes > 9) ? minutes : '0' + minutes}</span>:
            <span className="clockcount">{(seconds > 9) ? seconds : '0' + seconds}</span>   
        </Fragment>,
        document.getElementById('fs-clock'));
    }

    render() {
        setInterval(this.getClock, 1000);
        const settings = {
            dots: true,
            infinite: true,
            slidesToScroll: 2,
            slidesToShow: 4,
            childWidth: 290,
            interval: 5000,
            outerDim: {
                width: 1250,
                height: 440
            },
            innerDim: {
                width: 1160,
                height: 422
            }
        }
        
        return(
            <div className="flash-sale-container ji">
                <div className="fs-header">
                    <img src="assets/icons/flash.png" alt="flash-icon"/>
                    <span>FLASH SALE TỪ { this.getTime() } KẾT THÚC TRONG <span id="fs-clock"></span> HÔM NAY</span>
                    <img src="assets/icons/flash.png" alt="flash-icon"/>
                </div>
                <Slider {...settings}>
                    {
                        flashsales.map(item =>
                            <SingleProduct image={item.image}
                                href={item.href}
                                name={item.name}
                                salePrice={item.salePrice}
                                originPrice={item.originPrice} 
                                stars={item.stars}
                                numberOfRatings={item.numberOfRatings} />    
                        )
                    }
                </Slider>
                <button>XEM TẤT CẢ SẢN PHẨM</button>
            </div>
        );
    };
};

export default FlashSale;