import React, { Component } from 'react';
import Slider from '../../components/Slider/Slider';
import '../../styles/carousel.css';

class Carousel extends Component {
    render() {
        const settings = {
            dots: true,
            infinite: true,
            slidesToShow: 1,
            slidesToScroll: 1,
            childWidth: 725,
            interval: 1000,
            outerDim: {
                width: 725,
                height: 489
            },
            innerDim: {
                width: 725,
                height: 489
            }
        };
        return (
            <div className="carousel">
                <div className="carousel-text">
                    <p>Nike React Infinity Run</p>
                    <h1>ALL NEW SHADES</h1>
                    <button>Shop</button>
                </div>
                <Slider {...settings}>
                    <img src="assets/images/nike1.png" alt="carousel"/>
                    <img src="assets/images/nike2.png" alt="carousel"/>
                    <img src="assets/images/nike3.png" alt="carousel"/>
                    <img src="assets/images/nike4.png" alt="carousel"/>
                    <img src="assets/images/nike5.png" alt="carousel"/>
                </Slider>
            </div>
        );
    }
};

export default Carousel;