import React, { Component } from 'react';

export default class SearchBox extends Component {
    constructor(props) {
        super(props);
        this.state = {
            class: 'search',
            clicked: false
        }
        this.handleOnBlur = this.handleOnBlur.bind(this);
        this.handleOnFocus = this.handleOnFocus.bind(this);
    }

    handleOnFocus() {
        this.setState({
            class: this.state.class + ' is-focused',
            clicked: true
        })
    }

    handleOnBlur() {
        this.setState({
            class: 'search',
            clicked: false
        })    
    }

    render() {
        return (
            <div className={this.state.class} id="search">
                    <input onFocus={this.handleOnFocus} onBlur={this.handleOnBlur} placeholder="SEARCH"/>
                    <img src="assets/icons/search.png" alt="search-icon"/>
            </div>
        );
    }
}
