import React, { Component, Fragment } from 'react';
import '../../styles/products.css';
import Slider from '../Slider/Slider';
import ReactDOM from 'react-dom';
import { fashion, sports, versatile } from './Data';
import '../../styles/added-message.css';

export function SingleProduct(props) {
    const convertPrice = (number) => {
        return number.toString()
            .split('')
            .reverse()
            .map((num, i, arr) => 
                (i % 3 === 0 && i !== 0 && i !== arr.length) ? num + ',' : num
            )
            .reverse()
            .join('');
    };

    const convertStars = (stars) => {
        let reducer = stars;
        let result = [];
        while (result.length !== 5) {
            (reducer >= 0.75) ?
                result.push(<img src="assets/icons/star-full.png" alt="full-star-icon"/>) :
                (reducer >= 0.25) ? result.push(<img src="assets/icons/star-half.png" alt="haft-star-icon"/>) :
                    result.push(<img src="assets/icons/star-empty.png" alt="empty-star-icon"/>);
            reducer--;
        }
        return result;
    }

    function addToCart() {
        let cart = document.getElementById('cart-number');
        cart.innerText = Number(cart.innerText) + 1;

        const container = document.getElementsByClassName('content')[0].childNodes[1];
        
        const addedMess = (
            <div className="added-message-container jo" style={{height: window.innerHeight}}>
                <div className="added-message fo">
                    <div className="message">
                        <img src={props.image} alt="added-message"/>
                        <p>Đã thêm {props.name} vào giỏ hàng</p>
                    </div>
                </div>
            </div>
        );
        
        
            ReactDOM.render(addedMess, container);
            setTimeout(() => {
                ReactDOM.render(<Fragment />, container);
            }, 2000);
        

        function MyCreateElement(tag, props, ...rest) {
            let node = document.createElement(tag);
            for (let key in props) node[key] = props[key];
            // console.log(rest);
                rest.forEach(item => {
                if (typeof item === 'object') node.appendChild(item);
                else node.innerText += item;
            });
            return node;
          }
          
        let yourcart = document.getElementById('yourcart');
        
        const node = MyCreateElement("div", {
            className: "product-thumbnail"
            }, MyCreateElement("img", {
            className: "thumbnail",
            src: props.image
            }), MyCreateElement("div", {
            className: "details"
            }, MyCreateElement("div", {
            className: "header"
            }, MyCreateElement("span", {
            className: "name"
            }, props.name), MyCreateElement("span", {
            className: "price"
            }, convertPrice(props.salePrice), MyCreateElement("span", {
            className: "quantities"
            }, " x", 1))), MyCreateElement("div", {
            className: "footer"
            }, MyCreateElement("span", {
            className: "categorize"
            }, props.name), MyCreateElement("button", {
            className: "remove", onclick: removeFromCart
            }, "Xóa"))));
        
        document.getElementsByClassName('emptyMess')[0].style.display = 'none';
        yourcart.style.display = 'contents';
        yourcart.appendChild(node);

        if(yourcart.childNodes.length > 0) document.getElementsByClassName('recent-added')[0].style.display = "block";

        function removeFromCart() {
            let i, cart = document.getElementById('yourcart');
            for (i = 1; i < cart.childNodes.length; i++) {
                if (cart.childNodes[i].childNodes[1].childNodes[0].childNodes[0].innerText) {
                    break;
                }
            }
            let cartNum = document.getElementById('cart-number');
            cart.removeChild(cart.childNodes[i]);
            cartNum.innerText = Number(cartNum.innerText) - 1;
            if (cart.childNodes.length === 1) {
                document.getElementsByClassName('emptyMess')[0].style.display = 'inline';
                cart.style.display = 'flex';
                if(yourcart.childNodes.length === 1) document.getElementsByClassName('recent-added')[0].style.display = "none";
            }
            
        }
    }

    return(
        <div className="product-wrapper">
            <div className="product-image">
                <button><a href={props.href}>Mua ngay</a></button>
                <img role="button" src="assets/icons/cart.svg" alt="cart-icon" onClick={addToCart}/>
                <img src={props.image} alt="product"/>
                <div className="stars">
                    {convertStars(props.stars)}
                    <p>{'(' + props.stars.toFixed(1) + ' sao / ' + props.numberOfRatings.toFixed(0) + ' đánh giá)'}</p>
                </div>
            </div>
            <h2>{props.name}</h2>
            <p>{convertPrice(props.salePrice) + 'đ'}</p>
            <small>{convertPrice(props.originPrice) + 'đ'}</small>
        </div>
    );
}

class Category extends Component {
    constructor(props) {
        super(props);
        this.state = {
            cart: []
        }
        this.updateCart = this.updateCart.bind(this);
    }

    updateCart(name, price, quantity, catigorize, imgsrc) {
        const item = <h1>Hello</h1>
        ReactDOM.render(item, document.getElementsByClassName('cart-tooltip')[0]);
    }

    render() {
        const settings = {
            dots: true,
            infinite: false,
            slidesToScroll: 2,
            slidesToShow: 4,
            childWidth: 300,
            outerDim: {
                width: 1200,
                height: 420
            },
            innerDim: {
                width: 1180,
                height: 422
            }
        }
        
        return (
            <div className="products-container">
                { <h2>{ this.props.category.name }</h2> }
                <Slider {...settings}>
                {
                    this.props.category.data.map(item =>
                        <SingleProduct updateCart={this.updateCart} {...item} />    
                    )
                }
                </Slider>
                <button>XEM TẤT CẢ</button>
            </div>
        );
    };
};

class Products extends Component {
    render() {
        return (
            <div className="all-products">
                <Category category={fashion} />
                <Category category={sports} />
                <Category category={versatile} />
            </div>
        );
    };
};

export default Products;